@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@300;700;800;900&display=swap);
h1{
    text-align: center;
}


.sphere{
    width: 300px;
    height: 300px;
}

.main-container{
    height: 100vh;
    min-height: 100vh;
}

.landing-header{
    font-weight: 700;
    font-size: 60px;
}
.bg-dark{
    background-color: black !important;
}

.header{
    margin: 40px 0;
}

.sub-title{
    font-size: 20px;
    font-style: italic;
    margin-top: 10px;
}
#headshot{
    border-radius: 50%;
    height: 300px;
    width: 300px;
}

.headshot-container{
    margin-bottom: 30px;
}
.bg-project{
    background: #000000;
    border: 1px solid #ffffff;
}


.fa{
    font-size: 60px;
    color: white;
    margin-right: 40px;
}
.project-btn{
    color:white;
    border: 1px solid white;
}

.project-btn:hover{
    color: white;
    background: #3f3f3f
}

.tech-list{
    list-style: none;
    padding: 0;
}

.tech-list li{
    display: inline-block;
    border: 1px solid white;
    background: #272727;
    padding: 10px;
    border-radius: 5px;
    margin-right: 15px;
    margin-bottom: 20px;
}
.footer{
    text-align: center;
    width: 100%;
    height: 80px;
}

html, body{
    background-color: black;
    color: white;
    font-family: 'Kanit', sans-serif;
}

h1{
    font-weight: bold;
}





